{transform:translateX(100%)}}.card span:nth-child(2){position:absolute;top:0;right:0;width:2px;height:100%;background:linear-gradient(to bottom, transparent, #7dba5c);background:linear-gradient(to bottom, transparent, var(--first-accent-color));-webkit-animation:animate2 2s linear infinite;animation:animate2 2s linear infinite;-webkit-animation-delay:1s;animation-delay:1s}@-webkit-keyframes animate2{0%{transform:translateY(-100%)}100%{transform:translateY(100%)}}@keyframes animate2{0%{transform:translateY(-100%)}100%{transform:translateY(100%)}}.card span:nth-child(3){position:absolute;bottom:0;left:0;width:100%;height:2px;background:linear-gradient(to left, transparent, #7dba5c);background:linear-gradient(to left, transparent, var(--first-accent-color));-webkit-animation:animate3 2s linear infinite;animation:animate3 2s linear infinite}@-webkit-keyframes animate3{0%{transform:translateX(100%)}100%{transform:translateX(-100%)}}@keyframes animate3{0%{transform:translateX(100%)}100%{transform:translateX(-100%)}}.card span:nth-child(4){position:absolute;top:0;left:0;width:2px;height:100%;background:linear-gradient(to top, transparent, #7dba5c);background:linear-gradient(to top, transparent, var(--first-accent-color));-webkit-animation:animate4 2s linear infinite;animation:animate4 2s linear infinite;-webkit-animation-delay:1s;animation-delay:1s}@-webkit-keyframes animate4{0%{transform:translateY(100%)}100%{transform:translateY(-100%)}}@keyframes animate4{0%{transform:translateY(100%)}100%{transform:translateY(-100%)}}@media screen and (max-width: 600px){.card{margin:15px 0px 0px;max-width:100%}}

:root{--primary-color: #222;--secondary-color-darker: #111;--secondary-color-dark: #222;--secondary-color-light: #818181;--secondary-color-lighter: #bbb;--first-accent-color: #7dba5c}.card-container{width:100%;height:100vh;display:inline-flex;flex-direction:row;flex-wrap:wrap;overflow:auto;justify-content:center}

:root {
    --primary-color: #222;
    --secondary-color-darker: #111;
    --secondary-color-dark: #222;
    --secondary-color-light: #818181;
    --secondary-color-lighter: #bbb;
    --first-accent-color: #7dba5c;
}

@font-face {
  font-family: "oswald-light";
  src: url(/static/media/Oswald-Light.4be970a1.ttf);
}

@font-face {
  font-family: "oswald-extra-light";
  src: url(/static/media/Oswald-ExtraLight.309040b6.ttf);
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: var(--secondary-color-lighter);
  line-height: 22px;
  overflow-y: hidden;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  background: var(--primary-color);
  margin: 0;
}

h1,
h2,
h3 {
  margin-top: 16px;
  font-family: "Raleway", sans-serif;
  border-bottom: 0px solid #dedede;
  padding-bottom: 3px;
}

